import React from 'react'
import styled from 'styled-components'


const Container = styled.div`
  background:#e0c9a6;
  border-radius:8px;
  margin-bottom:64px;
  padding:48px 12px;
  text-align:center;
  margin:0 0px 32px 0px; 
`

const ClueText = styled.p`
 line-height:1.4em; 
 letter-spacing:0.5px;
 margin-bottom:16px;
`
const QuestionText = styled.p`
 line-height:1.4em; 
 font-weight:bold;
 letter-spacing:0.5px;
`

const ClueButtonContainer = styled.div`
margin-top:16px;
  display:flex;
  flex-direction:row;
  align-items:center;
  justify-content:center;

`

function Clue({answers, myScore, displayClue, getClue, colors, firstClues, team}) {
  let clue = answers[myScore.correct]
  return (
    <Container style={{background: '#E7EFEF'}}>
    {clue &&
    <React.Fragment>
    <ClueText>{clue.directions}</ClueText>
    <QuestionText>Question</QuestionText>
    <ClueText>{clue.question}</ClueText>
    {displayClue &&
    <React.Fragment>
      <QuestionText>Clue</QuestionText>
      <ClueText>{clue.clue}</ClueText>
    </React.Fragment>
    }
    {!displayClue &&
    <ClueButtonContainer onClick={getClue}>
      <QuestionText>Give me a clue (+ 5 mins)</QuestionText>
      <img style={{width:'24px', height: '24px'}}src={require('../assets/clue.png')} />
    </ClueButtonContainer>
    }
    </React.Fragment>
  }
      
    </Container>
    
  );
}

export default Clue;
