import React , {useState, useEffect }  from 'react'
import allColors from '../data/colors.js'
import allAnswers from '../data/answers'
import styled from 'styled-components'
import Input from '../components/Input'
import Button from '../components/Button'
import standardise from '../helpers/standardise'
import api from '../helpers/api'
import Validation from '../components/Validation'
import firstClues from '../data/firstClues'
import { getDatabase, ref, set, onValue } from "firebase/database";
import Scores from '../components/Scores'
import Overlay from '../components/Overlay'
import CallForHelp from '../components/CallForHelp'
import ClueAndResponse from '../components/ClueAndResponse'
import Header from '../components/Header'

const Container = styled.div`
  width:100%;
  height:100%;
  padding:100px 24px 0 24px;
  box-sizing:border-box;

`



const Background = styled.div`
  position:absolute;
  z-index:-1;
  opacity:0.9;
  top:0;
  left:0;
  right:0;
  bottom:0;
`

const ImageContainer = styled.div`
  width:100%;
  display:flex;
  align-items:center;
  justify-content:center;
`

const BeerImage = styled.img`
  margin:auto;
  max-width:75%;
`
const NextClueContainer = styled.div`
background:#E7EFEF;
padding:24px;
border-radius:20px;
`
const NextClueTime = styled.h2`
  font-size:52px;
  color:#275653;
  margin:0;
  text-align:center;
`

const NextClueEyebrow = styled.p`
// margin-bottom:12px;
margin:0;
  font-size:16px;
  color:#275653;
  text-align:center;
`

const NextClueHelp = styled.p`
  color: #717171;
  font-size:12px;
  margin-top:12px;
`


function Gameplay({team, database}) {


  const colors = allColors[team]
  const answers = allAnswers[team]
  const [inputValue, setInputValue] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [isIncorrect, setIsIncorrect ] = useState(false)
  const [overlay, setOverlay] = useState(false)
  const [scores, setScores] = useState(null)
  const [myScore, setMyScore] = useState(null)
  const [time, setTime] = useState(Date.now());
  const [displayClue, setDisplayClue] = useState(false)
  // Ensures refresh of page when not showing clue
  useEffect(() => {
    const interval = setInterval(() => setTime(Date.now()), 1000);
    return () => {
      clearInterval(interval);
    };
  }, []);

  useEffect(() => {
    const scoresRef = ref(database, 'scores');
    const myScoreRef = ref(database, `scores/${team}`);
    onValue(scoresRef, (snapshot) => {
      const data = snapshot.val();
      setScores(data)
    });
    onValue(myScoreRef, (snapshot) => {
      const data = snapshot.val();
      setMyScore(data)
      if (data && data.complete){
        setOverlay({header: "Congratulations", body: "You've finished!"})
      }
    });
    return () => {
      // scoresRef.off()
      // myScoreRef.off()
    }
  },[])

  const getClue = () => {
    updateScore('incorrect', 5)
    setDisplayClue(true)
  }


  const updateScore = (key, updateByValue) => {
    let value = updateByValue || 1
    const correct = key === 'correct' ? myScore.correct + value : myScore.correct
    const incorrect = key === 'incorrect' ? myScore.incorrect + value : myScore.incorrect
    const complete = correct === answers.length
    let myNewScore = {
      incorrect,
      correct,
      complete,
      bonus: key === 'bonus' ? updateByValue : myScore.bonus,
      totalTime: key === 'incorrect' ? myScore.totalTime + (1000*60) :  myScore.totalTime,
      startTime: myScore.startTime
    }
    const myScoreRef = ref(database, `scores/${team}`);
    set(myScoreRef, myNewScore)
  }

  const updateScoreAtCheckpoint = (nextStartTime) => {
    let myNewScore = {
      incorrect: myScore.incorrect,
      correct: myScore.correct +1,
      complete: false,
      totalTime: myScore.totalTime + (time - myScore.startTime),
      startTime: nextStartTime
    }
    const myScoreRef = ref(database, `scores/${team}`);
    set(myScoreRef, myNewScore)
  }


  const checkIfAtCheckpoint = () => {
    const nextAnswer = answers[myScore.correct +1]

    if (nextAnswer && nextAnswer.revealAtDate && nextAnswer.revealAtDate > time){
      return nextAnswer.revealAtDate
    } else {
      return false
    }
  }

  const checkAnswer = () => {
    const input = standardise(inputValue)
    let answerIndex = myScore ? myScore.correct : 0

    let answer = answers[answerIndex]
    if (!answer) return 
    let standardisedAnswer = standardise(answer.answer)
    if (standardisedAnswer === input){
      setOverlay({header: "Correct!", eyebrow: "Your answer was", imageSource: require('../assets/confetti.png')})
      setIsIncorrect(false)
      const nextStartTime = checkIfAtCheckpoint()
      if (nextStartTime){
        updateScoreAtCheckpoint(nextStartTime)
      } else {
        setDisplayClue(false)
        updateScore('correct')
      }
      return true 
    }  
    setInputValue('')
    setIsIncorrect(true)
    updateScore('incorrect')
    return false
  }

  const onSubmit = () => {
    console.log('submitting')
    if (inputValue.length > 0){
      // api()
      setIsLoading(true)
      let isCorrect = checkAnswer()
    }
  }

  const onCloseOverlay = () => {
    setOverlay(false)
    setInputValue('')
  }
console.log(answers[myScore?.correct]?.revealAtDate < time)

  return (
    <Container >
      <Background style={{background: 'white'}}/>
      <Header colors={colors}/>
      {myScore && (!answers[myScore.correct].revealAtDate || (answers[myScore.correct].revealAtDate < time)) ?
        <ClueAndResponse 
          {...{
           
            myScore,
            team, 
            getClue, 
            time,
            inputValue, 
            setInputValue, 
            database,
            answers,
            displayClue,
            colors,
            scores,
            onSubmit,
            overlay,
            onCloseOverlay
          }}
        /> :

        <Container>
          {myScore && answers[myScore.correct].revealAtDate &&
          <React.Fragment>
            <ImageContainer>
            <BeerImage src={require('../assets/santa.png')} />
            </ImageContainer>
            <NextClueContainer>
            <NextClueEyebrow>Your next clue will be available at:</NextClueEyebrow> 
            <NextClueTime>{answers[myScore.correct].revealAtHour.toString().padStart(2, "0")}:{answers[myScore.correct].revealAtMinute.toString().padStart(2, "0")}</NextClueTime> 
            </NextClueContainer>
            <NextClueHelp style={{textAlign:'center'}}>Your clock will restart then too. Have a drink</NextClueHelp> 
          </React.Fragment>
          }
        </Container>

      }

      
      
      <Scores scores={scores} time={time} myTeam={team}/>
      <CallForHelp />
      {overlay && 
        <Overlay {...overlay} colors={colors} onClose={onCloseOverlay}/>
      }


    </Container>
  );
}

export default Gameplay;
